import React, { Component } from 'react';
import { connect } from 'react-redux';

import Header from './../header';
import Nav from './../nav';
import Tbanner from './../Tbanner';
import Tinfo from './../Tinfo';
import Tprice from './../Tprice';
import Tuse from './../Tuse';
import Tcooperate from './../Tcooperate';
import Tcontact from './../Tcontact';
import ToTop from './../toTop';
import s from './Tob';

import { tobNumPost } from '../../actions/tobNum';

class Tob extends Component {
    componentWillMount() {
        const { dispatch } = this.props;
        dispatch(tobNumPost());
    }
    render() {
        const { forecast, tobNum } = this.props;
        if (!tobNum.data) {
            return (
                <div>loading</div>
            );
        } else {
            return (
                <div className={s.box}>
                    <Header data={forecast} />
                    <Nav />
                    <Tbanner data={tobNum} />
                    <Tinfo />
                    <Tprice />
                    <Tuse />
                    <Tcooperate />
                    <Tcontact />
                    <ToTop />
                </div>
            );
        }
    }
}

const mapStateToProps = (state) => {
    const { tobNum, forecast } = state;

    return { tobNum, forecast };
};

export default connect(mapStateToProps)(Tob);




